<script>
import html2canvas from "html2canvas";
export default {
  props: {
    id: { type: String },
  },
  data() {
    return {
      imgUrl: "",
      model: {
        company: "",
        code: null,
        address: "",
        route: "",
        contacts: {
          name: "",

          department: "",
          phoneNumber: null,
          email: "",
          qq: null,
        },
        container: "",
        medium: "",
        // 
        meter: null,
        otherCondition: "",
        meterType: "",
        useTime: "",
        liquidMedium: "",
        viscosity: "",
        desity: "",
        temperatureRange: "",
        pressureRange: "",
        liquidLevelRange: "",
        userPreferences: "",
        // 
        maxVoltage: {
          voltage: "",
          calibratingSignal: "",
        },
        minVoltage: {
          voltage: "",
          calibratingSignal: "",
        },
        electricity: "",
        liquidLevel: "",
        resistance: "",
        intrinsicPreferences: [],
        installationDiagram: "",
        // 
        time: "",
        contrastLiquidLevel: "",
        meterliquidLevel: "",
        meterSignal: "",
        signalFileName: "",
        mediumPressure: "",
        mediumTemperature: "",
        status: {
          value: null,
          label: "",
        },
        signalFigure: "",
        // 
        meterPanorama: "",
        meterClose: "",
        pipelinePanorama: "",
        probePanorama: "",
        // 
        supervisor: null,
        otherContent: "",
        // 
        reportStatus: "",
        // 
        submitter: "",
        // 
        scoreReport: null,
      },
      meter_info: {},
      submitter_info: {},
      supervisor_info: {},
      container_info: {},
      medium_info: {},
      scoreReport: "",
    };
  },
  computed: {
    thermalInsulation() {
      if (this.container_info.thermalInsulation) {
        return "";
      } else {
        return "";
      }
    },
  },
  methods: {
    // 
    async fetch() {
      const res = await this.$http.get(`rest/installationReports/${this.id}`);
      //   scoresundefined undefined.difficlut
      console.log(res);
      this.model = Object.assign({}, this.model, res.data[0]);
      console.log(222, this.model);
      this.meter_info = this.model.meter_info[0];
      this.submitter_info = this.model.submitter_info[0];
      this.supervisor_info = this.model.supervisor_info[0];
      this.medium_info = this.model.medium_info[0];
      this.container_info = this.model.container_info[0];
    },
    saveImg() {
      html2canvas(this.$refs.imageWrapper, {
        useCORS: true,
      }).then((canvas) => {
        this.imgURL = canvas.toDataURL();
        // console.log(999, this.imgURL);
        if (window.navigator.msSaveOrOpenBlob) {
          var bstr = atob(this.imgURL.split(",")[1]);
          var n = bstr.length;
          var u8arr = new Uint8Array(n);
          while (n--) {
            u8arr[n] = bstr.charCodeAt(n);
          }
          var blob = new Blob([u8arr]);
          window.navigator.msSaveOrOpenBlob(
            blob,
            "chart-download" + "." + "png"
          );
          this.$message({
            message: "",
            type: "success",
          });
        } else {
          // chrome
          const a = document.createElement("a");
          a.href = this.imgURL;
          a.setAttribute("download", "chart-download");
          a.click();
          this.$message({
            message: "",
            type: "success",
          });
        }
      });
    },
    async reject() {
      this.$prompt("", "", {
        confirmButtonText: "",
        cancelButtonText: "",
      })
        .then(async ({ value }) => {
          this.$message({
            type: "success",
            message: ": " + value,
          });
          this.model.returnReason = value;
          this.model.reportStatus = "";
          await this.$http.put(
            `rest/installationReports/${this.id}`,
            this.model
          );
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "",
          });
        });
    },
    // 
    async pass() {
      this.$prompt("", "", {
        confirmButtonText: "",
        cancelButtonText: "",
      })
        .then(async ({ value }) => {
          this.$message({
            type: "success",
            message: " ",
          });
          this.model.returnReason = "/";
          this.model.comments = value;
          this.model.reportStatus = "";
          await this.$http.put(
            `rest/installationReports/${this.id}`,
            this.model
          );
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "",
          });
        });

      // this.$confirm(``, "", {
      //   confirmButtonText: "",
      //   cancelButtonText: "",
      //   type: "",
      // }).then(async () => {
      //   this.model.reportStatus = "";
      //   await this.$http.put(`rest/installationReports/${this.id}`, this.model);
      // });
    },
  },
  created() {
    this.fetch();
  },
};
</script>

